@import url(https://fonts.googleapis.com/css?family=Raleway:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #F7Deee;
  color: #000;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 1.3em;
  /* font-weight: 100; */
  line-height: 1.5;
  text-align: center;
  box-sizing: border-box;
  width: 85vw;
  height: 100vh;
  margin: 0 auto;
}

img {
  border-radius: 0.5em;
  width: 100%;
}

a {
  padding: 0.4em;
  margin: 0.4em;
  text-decoration: none;
}

input, textarea {
  font-family: 'Raleway', sans-serif;
  padding: 0.6em;
  margin: 0.5em;
  /* font-size: 1em; */
  border-radius: 0.5em;
}

.links {
  font-size: 0.8em;
}
/* input[type="submit"] {
  background-color: #009900;
  color: #fff;
  font-weight: bold;
} */
.submit {
  background-color: #009900;
  color: #fff;
  font-weight: bold;
}

.inactive {
  background-color: #006999;
  color: rgb(255, 255, 255);
  font-weight: bold;
}

input.empty::-webkit-input-placeholder {
  color: #ff0055;
}

input.empty::-moz-placeholder {
  color: #ff0055;
}

input.empty:-ms-input-placeholder {
  color: #ff0055;
}

input.empty::-ms-input-placeholder {
  color: #ff0055;
}

input.empty::placeholder {
  color: #ff0055;
}

.form-selector {
  /* display: flex; */
  justify-content: space-around;
  width: 86%;
  margin: 0 auto;
}

form {
  text-align: center;
}

